.wines {
	position: relative;
	&__bg {
		position: absolute;
		&:nth-child(1) {
			right: 0;
			top: 550px;
		}
		&:nth-child(2) {
			left: 0;
			top: 90px;
		}
	}
}

.wines-block {
	width: 100%;
	position: relative;
	padding: 270px 0 0 0;
	&__containers {
		max-width: 1440px;
		width: 100%;
		margin: 0 auto;
		padding: 0 60px;
		display: flex;
		align-items: revert;
		justify-content: space-between;
		gap: 16px;
	}
	&__profile {
		width: 568px;
		height: 100%;
		background: #ffffff;
		box-shadow: 0 0 10px rgb(0 88 180 / 28%);
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		&-new {
			background: #003f80;
			width: 100px;
			height: 100px;
			border-radius: 50%;
			display: flex;
			align-items: center;
			justify-content: center;
			position: absolute;
			top: 30px;
			left: 30px;
			cursor: pointer;
		}
		&-items {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 100%;
			height: 100%;
			margin: 50px 0 50px 0;
			& img {
				width: unset;
				height: unset;
				object-fit: cover;
			}
		}
		&-status {
			display: flex;
			align-items: center;
			justify-content: flex-start;
			flex-direction: column;
			gap: 15px;
			position: absolute;
			bottom: 40px;
			right: 30px;
		}
		&-fortress {
			width: 118px;
			border: 1px solid #003f80;
			border-radius: 5px;
			padding: 12px 3px;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			gap: 4px;
			& p {
				font-family: PT Serif;
				font-style: normal;
				font-weight: normal;
				font-size: 24px;
				line-height: 30px;
				text-align: center;
				color: #003f80;
			}
			& span {
				font-family: PT Serif;
				font-style: normal;
				font-weight: normal;
				font-size: 24px;
				line-height: 30px;
				text-align: center;
				color: #003f80;
			}
		}
		&-volume {
			width: 118px;
			border: 1px solid #003f80;
			border-radius: 5px;
			padding: 12px 0;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			gap: 4px;
			& p {
				font-family: PT Serif;
				font-style: normal;
				font-weight: normal;
				font-size: 24px;
				line-height: 30px;
				text-align: center;
				color: #003f80;
			}
			& span {
				font-family: PT Serif;
				font-style: normal;
				font-weight: normal;
				font-size: 24px;
				line-height: 30px;
				text-align: center;
				color: #003f80;
			}
		}
	}
	&__info {
		width: 735px;
		height: unset;
		background: #ffffff;
		box-shadow: 0 0 10px rgb(0 88 180 / 28%);
		padding: 30px 53px;
		&-add {
			font-family: PT Serif;
			font-style: normal;
			font-weight: normal;
			font-size: 24px;
			line-height: 30px;
			text-align: justify;
			color: #000000;
		}
		&-title {
			font-family: TT Norms;
			font-style: normal;
			font-weight: normal;
			font-size: 48px;
			line-height: 60px;
			color: #021245;
		}
		&-wrappers {
			display: flex;
			align-items: flex-start;
			justify-content: space-between;
			width: 100%;
			margin: 80px 0 0 0;
		}
		&-status {
			margin: 5px 0 39px 0;
			display: flex;
			align-items: center;
			gap: 10px;
			justify-content: flex-start;
			flex-direction: column;
		}
		&-viv {
			background: #b1152d;
			border-radius: 27px;
			padding: 8px;
			display: flex;
			align-items: center;
			justify-content: center;
			& p {
				font-family: PT Serif;
				font-style: normal;
				font-weight: normal;
				font-size: 18px;
				line-height: 24px;
				margin: 0 0 0 5px;
				color: #ffffff;
			}
		}
		&-vivs {
			margin: 0 0 0 6px;
			background: #ffffff;
			border-radius: 27px;
			padding: 8px;
			display: flex;
			align-items: center;
			justify-content: center;
			& p {
				margin: 0 0 0 7px;
				font-family: PT Serif;
				font-style: normal;
				font-weight: normal;
				font-size: 16px;
				line-height: 21px;
				color: #b1152d;
			}
		}
		&-vin {
			margin: 0 0 0 12px;
			padding: 8px;
			background: #003f80;
			border-radius: 27px;
			display: flex;
			align-items: center;
			justify-content: flex-start;
			& p {
				font-family: PT Serif;
				font-style: normal;
				font-weight: normal;
				font-size: 18px;
				line-height: 24px;
				color: #ffffff;
			}
		}
		&-vins {
			margin: 0 0 0 6px;
			background: #ffffff;
			border-radius: 27px;
			padding: 8px;
			margin: 0 0 0 7px;
			display: flex;
			align-items: center;
			justify-content: center;
			& p {
				font-family: PT Serif;
				font-style: normal;
				font-weight: normal;
				font-size: 16px;
				line-height: 21px;
				margin: 0 0 0 6px;
				color: #003f80;
			}
		}
		&-description {
			margin: 30px 0;
			font-family: PT Serif;
			font-style: normal;
			font-weight: bold;
			font-size: 26px;
			line-height: 30px;
			color: #021245;
			& span {
				font-family: PT Sans;
				font-style: normal;
				font-weight: normal;
				font-size: 24px;
				line-height: 25px;
				color: #000000;
			}
		}
		&-author {
			font-family: PT Serif;
			font-style: normal;
			font-weight: bold;
			font-size: 26px;
			line-height: 30px;
			color: #021245;
			& span {
				font-family: PT Sans;
				font-style: normal;
				font-weight: normal;
				font-size: 24px;
				line-height: 25px;
				color: #000000;
			}
		}
		&-combination {
			display: flex;
			align-items: center;
			justify-content: flex-start;
			margin: 35px 0;
			& p {
				font-family: PT Serif;
				font-style: normal;
				font-weight: bold;
				font-size: 26px;
				line-height: 30px;
				color: #021245;
			}
		}
		&-combinations {
			width: 33px;
			height: 33px;
			background: #003f80;
			margin: 0 8px;
			border-radius: 50%;
			display: flex;
			align-items: center;
			justify-content: center;
			cursor: pointer;
		}
		&-catalog {
			display: flex;
			align-items: center;
			justify-content: flex-start;
		}
		&-items {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			margin: 0 30px 0 0;
			&:last-child {
				margin: 0 0 0 0;
			}
			& p {
				font-family: PT Serif;
				font-style: normal;
				font-weight: normal;
				font-size: 24px;
				line-height: 30px;
				color: #021245;
				margin: 17px 0 0 0;
			}
		}
		&-btns {
			display: flex;
			width: 100%;
			align-items: center;
			justify-content: flex-start;
			margin: 55px 0 0 0;
		}
		&-back {
			min-width: 240px;
			border-radius: 38.5px;
			padding: 14px 0;
			display: flex;
			align-items: center;
			justify-content: flex-start;
			cursor: pointer;
			background: transparent;
			position: absolute;
			z-index: 5;
			top: 185px;
			& p {
				font-family: TT Norms;
				font-style: normal;
				font-weight: normal;
				font-size: 24px;
				line-height: 20px;
				margin: 0 0 0 10px;
				color: #003f80;
			}
		}
		&-pay {
			min-width: 240px;
			background: #003f80;
			border: 3px solid #003f80;
			border-radius: 38.5px;
			padding: 6px 30px;
			display: flex;
			align-items: center;
			justify-content: center;
			cursor: pointer;
			& p {
				font-family: PTSerif;
				font-style: normal;
				font-weight: normal;
				font-size: 24px;
				line-height: 20px;
				color: #ffffff;
				margin: 0 0 0 20px;
			}
		}
	}
}
