*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  user-select: none;
}

*:focus {
  outline: none;
  border: none;
}

body {
  background-color: #fff;
  position: relative;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

input {
  padding: 0;
  margin: 0;
  border: 0;
  border: none;
}

button {
  padding: 0;
  border: none;
  cursor: pointer;
}

.containers {
  max-width: 1440px;
  padding: 0 60px;
  margin: 0 auto;
  width: 100%;
}

ol,
ul {
  padding-left: 0;
}

dl,
ol,
ul {
  margin-top: 0;
  margin-bottom: 0;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0;
}

.display-n {
  display: none !important;
}

.header {
  width: 100%;
  position: fixed;
  background: linear-gradient(180deg, #06417d 0%, rgba(6, 65, 125, 0) 100%);
  height: 155px;
  z-index: 100;
}

.header .containers {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  position: relative;
}

.header__menu {
  position: absolute;
  left: 60px;
  top: 30px;
  cursor: pointer;
}

.header__logo {
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0%);
  top: 50px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: 15px;
}

.header__lang {
  font-family: PTSerif;
  font-style: normal;
  font-weight: normal;
  font-size: 26px;
  line-height: 34px;
  color: #ffffff;
  position: absolute;
  right: 60px;
  top: 30px;
  cursor: pointer;
}

.header__circle {
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0%);
  top: 32px;
  width: 123px;
  height: 62px;
  border-radius: 100px 100px 0 0;
  border-top: 2px solid #fff;
  border-left: 2px solid #fff;
  border-right: 2px solid #fff;
}

.header__lines {
  width: 100%;
  position: absolute;
  overflow: hidden;
  height: 2px;
  top: 94px;
}

.header__line {
  border-bottom: 2px solid #fff;
  width: 100%;
  height: 2px;
  position: absolute;
}

.header__line:nth-child(1) {
  bottom: 0;
  width: 100%;
  left: calc(-50% - 59px);
}

.header__line:nth-child(2) {
  bottom: 0;
  width: 100%;
  right: calc(-50% - 59px);
}

.header__basket {
  background: #003f80;
  border: 2px solid #ffffff;
  border-radius: 39px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 3px 16px;
  min-width: 210px;
  position: absolute;
  top: 0;
  left: 15%;
  cursor: pointer;
  z-index: 5;
}

.header__basket h3 {
  font-family: Prata;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 45px;
  color: #ffffff;
  margin: 0 auto 0 0;
}

.header__order {
  background: #003f80;
  border: 2px solid #ffffff;
  border-radius: 39px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 3px 20px;
  min-width: 210px;
  position: absolute;
  top: 0;
  right: 15%;
  cursor: pointer;
  z-index: 5;
}

.header__order h3 {
  font-family: Prata;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 45px;
  color: #ffffff;
  margin: 0 auto 0 0;
}

.header__order p {
  position: absolute;
  font-family: PTSerif;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 45px;
  color: #ffffff;
  top: 50%;
  transform: translate(0%, -50%);
  right: 31px;
}

.header__btns {
  position: absolute;
  max-width: 1920px;
  padding: 0 60px;
  width: 100%;
  top: 67px;
}

.footer {
  width: 100%;
  padding: 70px 0 0 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

.footer__line {
  width: 72px;
  height: 1px;
  border-top: 1px solid #003f80;
}

.footer__title {
  margin: 35px 0 55px 0;
  font-family: Prata;
  font-style: normal;
  font-weight: normal;
  font-size: 35px;
  line-height: 25px;
  text-align: center;
  color: #003f80;
}

.footer__wrapper {
  width: 100%;
  position: relative;
}

.footer__wrapper-bg {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  z-index: -5;
}

.footer__wrapper-bg img {
  width: 100%;
  height: 100%;
}

.footer__wrapper::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 63, 128, 0.7);
  z-index: -3;
}

.footer__contact {
  display: flex;
  width: 100%;
  align-items: flex-start;
  justify-content: space-between;
  max-width: 1440px;
  padding: 75px 105px;
  margin: 0 auto;
}

.footer__contact-info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}

.footer__contact-info h3 {
  font-family: PTSerif;
  font-style: normal;
  font-weight: normal;
  font-size: 44px;
  line-height: 58px;
  color: #ffffff;
}

.footer__contact-info a {
  font-family: TTNorms;
  font-style: normal;
  font-weight: bold;
  font-size: 21px;
  line-height: 25px;
  letter-spacing: 0.09em;
  color: #ffffff;
  margin: 44px 0 0 0;
}

.footer__contact-info span {
  font-family: TTNorms;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 26px;
  letter-spacing: 0.09em;
  color: #ffffff;
  margin: 36px 0 0 0;
}

.footer__contact-info p {
  font-family: TTNorms;
  font-style: normal;
  font-weight: bold;
  font-size: 21px;
  line-height: 25px;
  letter-spacing: 0.09em;
  color: #ffffff;
  margin: 44px 0 0 0;
}

.footer__contact-btn {
  background: #003f80;
  border: 2px solid #ffffff;
  text-align: center;
  border-radius: 27px;
  padding: 14px 55px;
  cursor: pointer;
  font-family: Prata;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  letter-spacing: 0.1em;
  color: #ffffff;
  margin: 25px 0 0 0;
  transition: all 0.5s ease;
}

.footer__contact-btn:hover {
  color: #003f80;
  background: #ffffff;
  transition: all 0.5s ease;
}

.footer__contact-maps {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  width: 860px;
}

.footer__contact-maps iframe {
  width: 100%;
  height: 312px;
}

.footer__contact-maps-add {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: flex-start;
  margin: 30px 0 0 0;
}

.footer__contact-maps-add h3 {
  font-family: TTNorms;
  font-style: normal;
  font-weight: bold;
  font-size: 21px;
  line-height: 25px;
  letter-spacing: 0.09em;
  text-transform: uppercase;
  color: #ffffff;
}

.footer__contact-icons {
  width: 60px;
  height: 60px;
  background: #ffffff;
  border-radius: 7px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.footer__contact-icons:last-child {
  margin: 0 0 0 12px;
}

.footer__contact-icons:nth-child(2) {
  margin: 0 0 0 auto;
}

.footer__block {
  padding: 45px 0 32px 0;
  background: rgba(0, 63, 128, 0.95);
}

.footer__block-wrapper {
  max-width: 1440px;
  padding: 0 105px;
  margin: 0 auto;
  width: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.footer__block-contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

.footer__block-contact a {
  margin: 0 0 22px 0;
}

.footer__block-contact a:last-child {
  margin: 0;
}

.footer__block-list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 30px 0;
}

.footer__block-list a {
  font-family: TTNorms;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.09em;
  color: #ffffff;
}

.footer__block-items {
  margin: 0 0 15px 0;
}

.footer__block-items:last-child {
  margin: 0;
}

.footer__block-table {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  padding: 30px 0;
}

.footer__block-create {
  margin: auto 0;
}

.footer__block-create a {
  font-family: TTNorms;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.09em;
  color: #ffffff;
}

.footer__block-create a span {
  text-decoration: underline;
}

.js-select-lang {
  width: 70px;
  position: relative;
}

.js-select-lang .lang-select-placeholder {
  display: block;
  padding: 5px 0;
  width: 100%;
  height: 100%;
  position: relative;
  cursor: pointer;
}

.js-select-lang:after {
  display: none !important;
}

.js-select-lang .lang-select-placeholder:after {
  content: "";
  background: url("../svg/header/arrow-lang.svg") no-repeat;
  transition: all 0.5s ease;
  position: absolute;
  top: 50%;
  right: 8px;
  transform: translate(0, -50%);
  height: 8px;
  width: 16px;
}

.js-select-lang.lang-select-item-active .lang-select-placeholder:after {
  transition: all 0.5s ease;
  transform: translate(0, -50%) rotate(180deg);
}

.js-select-lang .lang-select-block {
  bottom: auto;
  top: calc(100% + 1px);
  position: absolute;
  left: 0;
  z-index: 5;
}

.js-select-lang.lang-select-item-active {
  /* border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  border-bottom: 0; */
}

.js-select-lang.lang-select-item-active .lang-select-block {
  padding: 20px;
  border-radius: 30px;
  height: auto;
  box-sizing: content-box;
  background: white;
  width: 100px;
  left: -40px;
}

.js-select-lang .lang-select-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  height: 120px;
  overflow-y: auto;
}

.js-select-lang .lang-select-wrapper::-webkit-scrollbar {
  width: 27px;
}

.js-select-lang .lang-select-wrapper::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 10px 10px #435179;
  box-shadow: inset 0 0 10px 10px #435179;
  border: solid 12px transparent;
}

.js-select-lang .lang-select-wrapper::-webkit-scrollbar-thumb {
  -webkit-box-shadow: inset 0 0 10px 10px #003f80;
  box-shadow: inset 0 0 10px 10px #003f80;
  border: solid 10px transparent;
}

.js-select-lang .lang-select-element {
  width: 100%;
  padding: 3px 0;
  background: 0 0;
  color: #000;
  border: none;
  margin: 0 0 5px 0;
  text-align: left;
  cursor: pointer;
}

.modelGalleryPhoto {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 21, 75, 0.5);
  backdrop-filter: blur(80px);
  overflow-x: hidden;
  overflow-y: auto;
  outline: 0;
  z-index: 1000000;
  display: flex;
  align-items: center;
  justify-content: center;
}

.modelGalleryPhoto__close {
  position: absolute;
  top: 50px;
  right: 50px;
  cursor: pointer;
  z-index: 20;
}

.modelGalleryPhoto__block {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.modelGalleryPhoto__block .swiper-wrapper {
  display: flex;
  align-items: center;
}

.modelGalleryPhoto__block .swiper-slide {
  width: 90%;
  height: 90%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.modelGalleryPhoto__block .swiper-slide img {
  object-fit: cover;
}

.modelGalleryPhoto__block .swiper-button-next {
  cursor: pointer;
  right: 3%;
}

.modelGalleryPhoto__block .swiper-button-next::after {
  content: none;
}

.modelGalleryPhoto__block .swiper-button-prev {
  cursor: pointer;
  left: 3%;
}

.modelGalleryPhoto__block .swiper-button-prev::after {
  content: none;
}

.modelBookingPhoto {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 21, 75, 0.5);
  backdrop-filter: blur(80px);
  overflow-x: hidden;
  overflow-y: auto;
