}

.modelBookingPhoto__wrapper {
  width: 1070px;
  padding: 35px 55px 55px 55px;
  background: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
  justify-content: flex-start;
}

.modelBookingPhoto__photo {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-areas: "modelBookingPhoto_1 modelBookingPhoto_2" "modelBookingPhoto_3 modelBookingPhoto_2";
  gap: 6px;
  width: 100%;
}

.modelBookingPhoto__photo img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.modelBookingPhoto__photo img:nth-child(1) {
  grid-area: modelBookingPhoto_1;
}

.modelBookingPhoto__photo img:nth-child(2) {
  grid-area: modelBookingPhoto_2;
}

.modelBookingPhoto__photo img:nth-child(3) {
  grid-area: modelBookingPhoto_3;
}

.modelBookingPhoto__close {
  position: absolute;
  top: -26px;
  right: -26px;
  cursor: pointer;
  z-index: 12;
}

.modelBookingPhoto__description {
  display: flex;
