@media (max-width: 1350px) {
    .wines-block__containers {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
    }
    .wines-block__profile {
        width: 100%;
    }
    .wines-block__info {
        width: 100%;
    }
}
@media (max-width: 1200px) {
    .wines-block__containers {
        padding: 0 20px;
    }
}
@media (max-width: 800px) {
    .wines-block__info-wrappers {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        gap: 35px;
    }
}
@media (max-width: 600px) {
    .wines-block__profile-fortress,
    .wines-block__profile-volume {
        width: 90px;
        border: 1px solid #003f80;
        border-radius: 5px;
        padding: 12px 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
        gap: 4px;
    }
    .wines-block__profile-fortress p,
    .wines-block__profile-volume p {
        font-family: PT Serif;
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 30px;
        text-align: center;
        color: #003f80;
    }
    .wines-block__profile-fortress span,
    .wines-block__profile-volume span {
        font-family: PT Serif;
        font-style: normal;
        font-weight: normal;
        font-size: 17px;
        line-height: 30px;
        text-align: center;
        color: #003f80;
    }
}
@media (max-width: 500px) {
    .wines-block__profile-items img {
        width: 40%;
        height: unset;
        object-fit: cover;
    }
    .wines-block__info-viv p {
        font-family: PT Serif;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 24px;
        margin: 0 0 0 5px;
    }
    .wines-block__profile-new {
        background: #003f80;
        width: 80px;
        height: 80px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 30px;
        left: 30px;
        cursor: pointer;
        & img {
            width: 90%;
        }
    }
    .wines-block__profile-status {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-direction: column;
        gap: 15px;
        position: absolute;
        bottom: 0px;
        right: 15px;
    }
    .wines-block__info {
        height: unset;
        background: #ffffff;
        box-shadow: 0 0 10px rgb(0 88 180 / 28%);
        padding: 30px 20px;
    }

}